.hero-img{
  width:80%;
  height: 70vh;
  top: 5rem;
  left: 130px;
  border-radius: 20px;
  position: relative;
  background-color:black;
  margin-top: 10rem;
  margin-bottom: 10rem;
   display: flex;
  flex-direction: row;
  justify-items: center;
  align-items: center;
  justify-content: center;
  column-gap: 1rem;
  animation:move 2s infinite;
}
  
.Image img {
   border-radius: 15%;
   padding: 2rem;
  }

 h1{
  display: inline;
  text-align: center;
  align-items: center;
  margin: 5rem;
  color: #7f00ff;
  }
p{
  margin: 1rem;
  padding: 1rem;
  width: 80%;
  line-height: 2rem;
}
  .heading a i{
    font-size: 6rem;
    margin-left: 14rem;
    align-items: center;
    text-align: center;
  }
  @media (max-width:687px) {
    .hero-img{
      width:95%;
      height: 70vh;
      left: 10px;
      border-radius: 20px;
      margin-top: 0rem;
      margin-bottom: 10rem;
       display: flex;
      flex-direction: column;
      column-gap: 1rem;
    }
    .heading a i{
     margin-left: 4rem;
    }
  }